import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { GetElectronicAddress, GetPhoneAddress, GetPostalAddress } from '../../../common-types';

@Component({
  template: '',
})
export class ManageProfileFormComponent implements OnInit {
  /**
   * The list of address types.
   */
  @Input() types: Array<string> | undefined;
  /**
   * The data for the address entry.
   */
  @Input() data: GetPhoneAddress | GetElectronicAddress | GetPostalAddress | undefined;
  /**
   * The number of addresses stored.
   */
  @Input() count: number | undefined;
  /**
   * Whether the form is being submitted.
   * Defaults to false.
   */
  @Input() isSubmitting = false;
  @Output() cancel = new EventEmitter<void>();
  @Output() delete = new EventEmitter<GetPhoneAddress | GetElectronicAddress | GetPostalAddress>();
  @Output() save = new EventEmitter<GetPhoneAddress | GetElectronicAddress | GetPostalAddress>();

  formData: FormGroup | undefined;
  showFormErrors = false;
  showDeleteButton = false;
  isCancelModalOpen = false;
  isDeleteModalOpen = false;
  hasBeenSubmitted = false;

  ngOnInit() {
    if ((this.data as GetPhoneAddress | GetElectronicAddress | GetPostalAddress).key) {
      this.showDeleteButton = !!(this.count && this.count > 1);
    }
    this.createForm();
  }

  get form() {
    if (!this.formData) {
      throw new Error('formData is not defined');
    }

    return this.formData;
  }

  handleSubmit() {
    this.hasBeenSubmitted = true;
    this.markFormAsTouched();
    this.showErrorsOnForm();
    if (!this.form.valid) {
      return;
    }

    this.form.updateValueAndValidity();
    this.save.emit(this.form.value);
  }

  handleCancelClick() {
    if (this.form.touched) {
      this.isCancelModalOpen = true;
    } else {
      this.cancel.emit();
    }
  }

  closeCancelModal() {
    this.isCancelModalOpen = false;
    this.cancel.emit();
  }

  dismissCancelModal() {
    this.isCancelModalOpen = false;
    this.markFormAsTouched();
    this.showErrorsOnForm();
  }

  handleDelete() {
    this.isDeleteModalOpen = true;
  }

  closeDeleteModal() {
    this.isDeleteModalOpen = false;
    this.delete.emit(this.form.value);
  }

  dismissDeleteModal() {
    this.isDeleteModalOpen = false;
    this.markFormAsTouched();
    this.showErrorsOnForm();
  }

  private showErrorsOnForm() {
    this.showFormErrors = !this.form.valid;
  }

  protected markFormAsTouched() {}

  protected createForm() {}
}
