import { Directive, Input, OnDestroy, OnInit } from '@angular/core';
import { CountryCode } from '@backbase/phone-number-lib';
import { PhoneNumberPipe } from '@backbase/ui-ang/phone-number-format-pipe';
import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import {
  ManageProfilePreferencesService,
  PhoneNumberInputPreferences,
} from '../../services/manage-profile/manage-profile-preferences.service';
import { InputBase } from '../input-base/input-base';

@Directive({
  selector: '[bbUserPhoneNumberInputBase]',
})
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export class PhoneNumberInputBase extends InputBase implements OnInit, OnDestroy {
  private phoneNumberSubscription: Subscription | undefined;
  private preferences: PhoneNumberInputPreferences | undefined;

  @Input() phoneNumberCountryCode: CountryCode | undefined;
  @Input() phoneNumberDebounceTime: number | undefined;
  @Input() enablePhoneNumberFormatter: boolean | undefined;

  private readonly phoneNumberPreferencesSubscription: Subscription = this.manageProfilePreferencesService
    .getPhoneNumberInputPreferences()
    .subscribe((preferences: PhoneNumberInputPreferences) => {
      if (preferences) {
        this.preferences = preferences;
        this.preferences.debounceTime = this.preferences.debounceTime * 1000;
      }
    });

  constructor(
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly phoneNumberFormatter: PhoneNumberPipe,
  ) {
    super();
  }

  // eslint-disable-next-line complexity
  ngOnInit() {
    if (this.enablePhoneNumberFormatter && this.phoneNumberDebounceTime === undefined) {
      throw new Error(`phoneNumberDebounceTime is required when enablePhoneNumberFormatter is true`);
    }

    if (this.enablePhoneNumberFormatter && this.phoneNumberCountryCode === undefined) {
      throw new Error(`phoneNumberCountryCode is required when enablePhoneNumberFormatter is true`);
    }

    if (this.enablePhoneNumberFormatter !== false) {
      if (this.enablePhoneNumberFormatter || (this.preferences || {}).enablePhoneNumberFormatter) {
        this.phoneNumberSubscription = this.fieldControl.valueChanges
          .pipe(
            distinctUntilChanged(),
            debounceTime(
              (this.phoneNumberDebounceTime && this.phoneNumberDebounceTime * 1000) || this.preference.debounceTime,
            ),
          )
          .subscribe((value) => {
            value = this.phoneNumberFormatter.transform(
              value,
              this.phoneNumberCountryCode || this.preference.countryCode,
              true,
            );
            this.fieldControl.setValue(value);
          });
      }
    }
  }

  get preference() {
    if (!this.preferences) {
      throw new Error('preferences are not defined');
    }

    return this.preferences;
  }

  ngOnDestroy() {
    this.phoneNumberPreferencesSubscription.unsubscribe();
    if (this.phoneNumberSubscription) {
      this.phoneNumberSubscription.unsubscribe();
    }
  }
}
