import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { GetPhoneAddress } from '../../../common-types';

@Component({
  template: '',
})
export class ManagePhoneNumbersComponent implements OnInit {
  /**
   * The list of phone numbers stored.
   */
  @Input() phoneNumbers: Array<GetPhoneAddress> | undefined;
  /**
   * The list of phone number types.
   */
  @Input() phoneNumberTypes: Array<string> | undefined;
  /**
   * The maximum number of allowed phone numbers.
   */
  @Input() maxPhoneNumbers: number | undefined;
  /**
   * Whether the manage button is disabled.
   */
  @Input() disableManage: boolean | undefined;
  @Input() dismissTimeout: number | undefined;
  @Output() updateProfile = new EventEmitter<void>();
  @Output() managePhoneNumber = new EventEmitter<void>();
  @Output() cancelManagePhoneNumber = new EventEmitter<void>();
  isManagingPhoneNumberIndex: number | undefined = undefined;
  isAddingPhoneNumber = false;
  isAddButtonAvailable = true;

  ngOnInit(): void {
    if (this.phoneNumbers && this.maxPhoneNumbers) {
      this.isAddButtonAvailable = this.phoneNumbers.length < this.maxPhoneNumbers;
    }
  }

  handleManagePhoneNumberOpen(index?: number) {
    this.isManagingPhoneNumberIndex = index;
    this.managePhoneNumber.emit();
  }

  handleAddNewPhoneNumber() {
    if (this.disableManage) {
      return;
    }
    this.isAddingPhoneNumber = true;
    this.managePhoneNumber.emit();
  }

  handleManagePhoneNumberClosed() {
    this.isManagingPhoneNumberIndex = undefined;
    this.isAddingPhoneNumber = false;
    this.cancelManagePhoneNumber.emit();
  }

  updateUserProfileData() {
    this.updateProfile.emit();
  }
}
