import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { UserProfile } from '../../common-types';
import {
  ManageProfilePreferencesService,
  WidgetPreferences,
} from '../../services/manage-profile/manage-profile-preferences.service';

@Component({
  template: '',
})
export class ManageProfileComponent implements OnDestroy, OnInit {
  @Input() notificationDismissTime: number | undefined;

  userProfile: UserProfile | undefined;
  loadingState = true;
  loadingErrorState = false;
  loadingError: HttpErrorResponse | undefined;
  disableAddressManaging = false;

  /** @deprecated */
  readonly widgetPreferences: Observable<WidgetPreferences> =
    this.manageProfilePreferencesService.getWidgetPreferences();

  protected readonly userProfileSubject = new BehaviorSubject(undefined);
  private userProfileObservable: Observable<UserProfile> | undefined;
  private userProfileSubscription: Subscription | undefined;

  refreshProfileData() {
    this.handleManageAddressClose();
    this.userProfileSubject.next(undefined);
  }

  handleManageAddressOpen() {
    this.disableAddressManaging = true;
  }

  handleManageAddressClose() {
    this.disableAddressManaging = false;
  }

  readonly handleProfileError = (error: HttpErrorResponse) => {
    this.loadingState = false;
    this.loadingErrorState = true;
    this.loadingError = error;
    this.cd.markForCheck();

    return new Observable<UserProfile>();
  };

  readonly onProfileUpdate = () => {
    this.loadingState = true;
    this.loadingError = undefined;
  };

  protected setUserProfileObservable() {
    return this.userProfileSubject.asObservable().pipe(
      map(
        () =>
          ({
            fullName: '',
          } as UserProfile),
      ),
    );
  }

  constructor(
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
  ) {}

  ngOnInit(): void {
    this.userProfileObservable = this.setUserProfileObservable();
    this.userProfileSubscription = this.userProfileObservable.subscribe((userProfileData: UserProfile) => {
      this.userProfile = userProfileData;
      this.loadingState = false;
      this.loadingErrorState = false;
      this.cd.markForCheck();
    });
  }

  ngOnDestroy() {
    if (this.userProfileSubscription) {
      this.userProfileSubscription.unsubscribe();
    }
  }
}
