import { Component, Input, OnInit } from '@angular/core';
import { ElectronicAddress } from '../../../common-types';
import { NewAddressComponent } from '../new-address/new-address.component';

@Component({
  template: '',
})
export class NewEmailAddressComponent extends NewAddressComponent implements OnInit {
  /**
   * The list of email address types.
   */
  @Input() emailAddressTypes: Array<string> | undefined;

  newEmailAddressData: ElectronicAddress | undefined;

  ngOnInit(): void {
    this.newEmailAddressData = {
      type: this.emailAddressTypes && this.emailAddressTypes.length ? this.emailAddressTypes[0] : '',
      address: '',
    };
  }
}
