import { Component } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { GetPhoneAddress } from '../../../common-types';
import { ManageProfileFormComponent } from './manage-profile-form.component';

@Component({
  template: '',
})
export class ManageProfilePhoneNumberFormComponent extends ManageProfileFormComponent {
  constructor(protected readonly formBuilder: FormBuilder) {
    super();
  }

  protected markFormAsTouched() {
    this.form.controls.type.markAsTouched();
    this.form.controls.number.markAsTouched();
    this.form.controls.primary.markAsTouched();
  }

  protected createForm() {
    const { key, type, number, primary } = this.data as GetPhoneAddress;

    this.formData = this.formBuilder.group({
      key,
      type: [type, Validators.required],
      number: [number, Validators.required],
      primary,
    });
  }
}
