import { Injectable } from '@angular/core';
import { GetElectronicAddress, GetPhoneAddress, GetPostalAddress, UserProfile } from '@backbase/data-ang/user';

@Injectable()
export class ManageProfileService {
  sortUserContactListsByPrimary = (profile: UserProfile): UserProfile => {
    this.sortListByPrimary(profile['electronic-addresses']);
    this.sortListByPrimary(profile['phone-addresses']);
    this.sortListByPrimary(profile['postal-addresses']);

    return profile;
  };

  private sortListByPrimary(list?: Array<GetElectronicAddress | GetPhoneAddress | GetPostalAddress>) {
    if (list) {
      list.sort((a, b) => {
        if (a.primary === b.primary) {
          return 0;
        }

        return a.primary ? -1 : 1;
      });
    }
  }
}
