import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'bb-cancel-modal',
  templateUrl: './cancel-modal.component.html',
})
export class CancelModalComponent {
  /**
   * An event emitter to signal continue has been pressed
   */
  @Output() continue = new EventEmitter<void>();
  /**
   * An event emitter to signal discard has been pressed
   */
  @Output() discard = new EventEmitter<void>();
  /**
   * An input which tells the component if it should open the modal
   */
  @Input() isOpen: boolean | undefined;

  dismissModal() {
    this.continue.emit();
  }
  closeModal() {
    this.discard.emit();
  }
}
