import { Component, EventEmitter, Input, Output } from '@angular/core';
import { GetPostalAddress } from '../../../common-types';

@Component({
  template: '',
})
export class ManagePostalAddressComponent {
  /**
   * The list of postal addresses stored.
   */
  @Input() postalAddress: GetPostalAddress | undefined;
  /**
   * The number of postal addresses stored.
   */
  @Input() postalAddressCount: number | undefined;
  /**
   * The list of postal address types.
   */
  @Input() postalAddressTypes: Array<string> | undefined;
  /**
   * Whether the manage button is disabled.
   */
  @Input() disableManage: boolean | undefined;
  @Input() dismissTimeout: number | undefined;
  @Output() updateProfile = new EventEmitter<void>();
  @Output() managePostalAddress = new EventEmitter<void>();
  @Output() cancelManagePostalAddress = new EventEmitter<void>();
  managingPostalAddress = false;

  handleManagePostalAddress() {
    if (this.disableManage) {
      return;
    }
    this.managingPostalAddress = true;
    this.managePostalAddress.emit();
  }

  handleCloseEditPostalAddressAfterSave() {
    this.updateProfile.emit();
  }

  handleCloseEditPostalAddress() {
    this.managingPostalAddress = false;
    this.disableManage = false;
    this.cancelManagePostalAddress.emit();
  }
}
