import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { GetPostalAddress } from '../../../common-types';

@Component({
  template: '',
})
export class ManagePostalAddressesComponent implements OnInit {
  /**
   * The list of postal addresses stored.
   */
  @Input() postalAddresses: Array<GetPostalAddress> | undefined;
  /**
   * The list of postal address types.
   */
  @Input() postalAddressTypes: Array<string> | undefined;
  /**
   * The maximum number of allowed postal addresses.
   */
  @Input() maxPostalAddresses: number | undefined;
  /**
   * Whether the manage button is disabled.
   */
  @Input() disableManage: boolean | undefined;
  @Input() dismissTimeout: number | undefined;
  @Output() updateProfile = new EventEmitter<void>();
  @Output() managePostalAddress = new EventEmitter<void>();
  @Output() cancelManagePostalAddress = new EventEmitter<void>();
  isManagingPostalAddressIndex: number | undefined = undefined;
  isAddingPostalAddress = false;
  isAddButtonAvailable = true;

  ngOnInit(): void {
    if (this.postalAddresses && this.maxPostalAddresses) {
      this.isAddButtonAvailable = this.postalAddresses.length < this.maxPostalAddresses;
    }
  }

  handleManagePostalAddressOpen(index?: number) {
    this.isManagingPostalAddressIndex = index;
    this.managePostalAddress.emit();
  }

  handleAddNewPostalAddress() {
    if (this.disableManage) {
      return;
    }
    this.isAddingPostalAddress = true;
    this.managePostalAddress.emit();
  }

  handleManagePostalAddressClosed() {
    this.isManagingPostalAddressIndex = undefined;
    this.isAddingPostalAddress = false;
    this.cancelManagePostalAddress.emit();
  }

  updateUserProfileData() {
    this.updateProfile.emit();
  }
}
