import { Component, Input, OnInit } from '@angular/core';
import { PhoneAddress } from '../../../common-types';
import { NewAddressComponent } from '../new-address/new-address.component';

@Component({
  template: '',
})
export class NewPhoneNumberComponent extends NewAddressComponent implements OnInit {
  /**
   * The list of phone number types.
   */
  @Input() phoneNumberTypes: Array<string> | undefined;

  newPhoneNumberData: PhoneAddress | undefined;

  ngOnInit(): void {
    this.newPhoneNumberData = {
      type: this.phoneNumberTypes && this.phoneNumberTypes.length ? this.phoneNumberTypes[0] : '',
      number: '',
    };
  }
}
