import { Component, EventEmitter, Input, Output } from '@angular/core';
import { GetElectronicAddress } from '../../../common-types';

@Component({
  template: '',
})
export class ManageEmailAddressComponent {
  /**
   * The data for the email address entry.
   */
  @Input() electronicAddress: GetElectronicAddress | undefined;
  /**
   * The number of email addresses stored.
   */
  @Input() emailAddressCount: number | undefined;
  /**
   * The list of email address types.
   */
  @Input() emailAddressTypes: Array<string> | undefined;
  /**
   * Whether the manage button is disabled.
   */
  @Input() disableManage: boolean | undefined;
  @Input() dismissTimeout: number | undefined;
  @Output() updateProfile = new EventEmitter<void>();
  @Output() manageEmail = new EventEmitter<void>();
  @Output() cancelManageEmail = new EventEmitter<void>();
  managingEmailAddress = false;

  handleManageEmailAddress() {
    if (this.disableManage) {
      return;
    }
    this.managingEmailAddress = true;
    this.manageEmail.emit();
  }

  handleCloseEditEmailAddressAfterSave() {
    this.updateProfile.emit();
  }

  handleCloseEditEmailAddress() {
    this.managingEmailAddress = false;
    this.disableManage = false;
    this.cancelManageEmail.emit();
  }
}
