import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { GetPhoneAddress } from '../../../common-types';
import {
  ManageProfilePreferencesService,
  PhoneNumberInputPreferences,
} from '../../../services/manage-profile/manage-profile-preferences.service';

@Component({
  template: '',
})
export class ManagePhoneNumberComponent {
  /**
   * The data for the phone number entry.
   */
  @Input() phoneNumber: GetPhoneAddress | undefined;
  /**
   * The number of phone numbers stored.
   */
  @Input() phoneNumberCount: number | undefined;
  /**
   * The list of phone number types.
   */
  @Input() phoneNumberTypes: Array<string> | undefined;
  /**
   * Whether the manage button is disabled.
   */
  @Input() disableManage: boolean | undefined;
  @Input() dismissTimeout: number | undefined;
  @Output() updateProfile = new EventEmitter<void>();
  @Output() managePhoneNumber = new EventEmitter<void>();
  @Output() cancelManagePhoneNumber = new EventEmitter<void>();
  managingPhoneNumber = false;

  /** @deprecated */
  phoneNumberPreferences: Observable<PhoneNumberInputPreferences> =
    this.manageProfilePreferencesService.getPhoneNumberInputPreferences();

  constructor(protected readonly manageProfilePreferencesService: ManageProfilePreferencesService) {}

  handleManagePhoneNumber() {
    if (this.disableManage) {
      return;
    }
    this.managingPhoneNumber = true;
    this.managePhoneNumber.emit();
  }

  handleCloseEditPhoneNumberAfterSave() {
    this.updateProfile.emit();
  }

  handleCloseEditPhoneNumber() {
    this.managingPhoneNumber = false;
    this.disableManage = false;
    this.cancelManagePhoneNumber.emit();
  }
}
