import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {
  CreateIdentityRequest,
  CreateIdentityResponse,
  GetIdentity,
  GetIdentitySessions,
  GetPendingApproval,
  IdentityManagementService,
  UpdateIdentityRequest,
  UserManagementService,
} from '@backbase/data-ang/user';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { UserActions } from '../../types/user-actions.enum';
import { ApprovalType } from '../../types/user-approval-type.enum';

@Injectable()
export class UserDataService {
  constructor(
    private readonly userManagementService: UserManagementService,
    private readonly identityManagementService: IdentityManagementService,
  ) {}

  checkUsernameAvailability(externalId: string): Observable<boolean> {
    return this.userManagementService.checkUserExists({ externalId }, 'response').pipe(
      map(this.isNotFound),
      catchError((error) => of(this.isNotFound(error))),
    );
  }

  createUser(createIdentityRequest: CreateIdentityRequest): Observable<CreateIdentityResponse> {
    return this.identityManagementService.createIdentity({ createIdentityRequest });
  }

  getPendingApprovalsByInternalIdAndType(internalId: string, type: ApprovalType): Observable<GetPendingApproval[]> {
    return this.getPendingApprovalsByInternalId(internalId).pipe(
      map((approvals) => approvals.filter((approval) => approval.type === type)),
    );
  }

  getPendingApprovalsByInternalId(internalId: string): Observable<GetPendingApproval[]> {
    return this.identityManagementService.getIdentityPendingApprovals({ internalId });
  }

  getUsersIdentitiesSessionsByInternalId(internalId: string): Observable<GetIdentitySessions> {
    return this.identityManagementService.getSessions({ internalId });
  }

  updateUsersIdentitiesSessionsLogoutRecordByInternalId(internalId: string, sessionIds: Array<string> = []) {
    return this.identityManagementService.logoutSessions({
      internalId,
      identityLogoutRequest: { sessionIds },
    });
  }

  updateUserActions(internalId: string, actions: UserActions[]): Observable<any> {
    return this.identityManagementService.sendRequiredActions({
      internalId,
      identityRequiredActionsRequest: { actions },
    });
  }

  updateUserRecordActions(internalId: string, updateIdentityRequest: UpdateIdentityRequest): Observable<any> {
    return this.identityManagementService.updateIdentity({
      internalId,
      updateIdentityRequest,
    });
  }

  changeUserPassword(existingPassword: string, newPassword: string): Observable<any> {
    return this.identityManagementService.changePassword({
      changePasswordRequest: {
        existingPassword,
        newPassword,
      },
    });
  }

  getUserDetails(internalId: string): Observable<GetIdentity> {
    return this.identityManagementService.getIdentity({ internalId });
  }

  private isNotFound(response: HttpResponse<any>) {
    return response.status === 404;
  }
}
