import { Injectable, Optional } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';
import { CountryCode } from '@backbase/ui-ang/phone-number-format-pipe';
import { combineLatest, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

export interface DefaultProperties {
  notificationDismissTime: number;
  enablePhoneNumberFormatter: boolean;
  phoneNumberCountryCode: CountryCode;
  phoneNumberDebounceTime: number;
  lockUnlockUserAccountCommentRequired: boolean;
  emailAddressTypes: Array<string>;
  postalAddressTypes: Array<string>;
  phoneNumberTypes: Array<string>;
  maxEmailAddresses: number;
  maxPhoneNumbers: number;
  maxPostalAddresses: number;
  legalEntityDebounceTime: number;
  usernameDebounceTime: number;
  requiredUserActionsDebounceTime: number;
  legalEntityResponseSize: number;
  showWidgetHeader: boolean;
}

const defaultProperties: DefaultProperties = {
  notificationDismissTime: 3,
  enablePhoneNumberFormatter: true,
  phoneNumberCountryCode: 'CA',
  phoneNumberDebounceTime: 1,
  lockUnlockUserAccountCommentRequired: false,
  emailAddressTypes: [''],
  postalAddressTypes: [''],
  phoneNumberTypes: [''],
  maxEmailAddresses: 3,
  maxPhoneNumbers: 3,
  maxPostalAddresses: 3,
  legalEntityDebounceTime: 1,
  usernameDebounceTime: 1,
  requiredUserActionsDebounceTime: 1,
  legalEntityResponseSize: 5,
  showWidgetHeader: true,
};

export interface WidgetPreferences {
  emailAddressTypes: Array<string>;
  postalAddressTypes: Array<string>;
  phoneNumberTypes: Array<string>;
  maxEmailAddresses: number;
  maxPhoneNumbers: number;
  maxPostalAddresses: number;
  showWidgetHeader: boolean;
}

export interface PhoneNumberInputPreferences {
  debounceTime: number;
  countryCode: CountryCode;
  enablePhoneNumberFormatter?: boolean;
}

@Injectable()
export class ManageProfilePreferencesService {
  constructor(@Optional() private readonly model?: ItemModel) {}

  getPhoneNumberInputPreferences(): Observable<PhoneNumberInputPreferences> {
    return combineLatest([
      this.getPhoneNumberDebounceTimePreference(),
      this.getPhoneNumberCountryCodePreference(),
      this.getPhoneNumberFormatPreference(),
    ]).pipe(
      map(([debounceTime, countryCode, enablePhoneNumberFormatter]: [number, CountryCode, boolean]) => ({
        debounceTime,
        countryCode,
        enablePhoneNumberFormatter,
      })),
    );
  }

  private getPhoneNumberDebounceTimePreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('phoneNumberDebounceTime', defaultProperties.phoneNumberDebounceTime)
      : of(defaultProperties.phoneNumberDebounceTime);
  }

  private getPhoneNumberCountryCodePreference(): Observable<CountryCode> {
    return this.model ? this.model.property<CountryCode>('phoneNumberCountryCode', 'CA') : of('CA');
  }

  private getPhoneNumberFormatPreference(): Observable<boolean> {
    return this.model
      ? this.model
          .property<string>('enablePhoneNumberFormatter', JSON.stringify(defaultProperties.enablePhoneNumberFormatter))
          .pipe(map((value) => value === 'true'))
      : of(defaultProperties.enablePhoneNumberFormatter);
  }

  getNotificationDismissTimePreference(): Observable<number> {
    return this.model
      ? this.model.property('notificationDismissTime', defaultProperties.notificationDismissTime)
      : of(defaultProperties.notificationDismissTime);
  }

  getLockUnlockAccountCommentRequiredPreference(): Observable<boolean> {
    return this.model
      ? this.model.property<boolean>(
          'lockUnlockUserAccountCommentRequired',
          defaultProperties.lockUnlockUserAccountCommentRequired,
        )
      : of(defaultProperties.lockUnlockUserAccountCommentRequired);
  }

  getWidgetPreferences(): Observable<WidgetPreferences> {
    const preferenceObservables: Observable<any>[] = [
      this.getEmailAddressTypePreferences(),
      this.getPostalAddressTypePreferences(),
      this.getPhoneNumberTypePreferences(),
      this.getMaxEmailAddressesPreference(),
      this.getMaxPhoneNumbersPreference(),
      this.getMaxPostalAddressesPreference(),
      this.getShowWidgetHeaderPreference(),
    ];

    return combineLatest(preferenceObservables).pipe(
      map(
        ([
          emailAddressTypes,
          postalAddressTypes,
          phoneNumberTypes,
          maxEmailAddresses,
          maxPhoneNumbers,
          maxPostalAddresses,
          showWidgetHeader,
        ]: any[]) => ({
          emailAddressTypes,
          postalAddressTypes,
          phoneNumberTypes,
          maxEmailAddresses,
          maxPhoneNumbers,
          maxPostalAddresses,
          showWidgetHeader,
        }),
      ),
    );
  }

  private getEmailAddressTypePreferences(): Observable<Array<string>> {
    return this.getModelToSeparatedStringArray('emailAddressTypes');
  }

  private getPostalAddressTypePreferences(): Observable<Array<string>> {
    return this.getModelToSeparatedStringArray('postalAddressTypes');
  }

  private getPhoneNumberTypePreferences(): Observable<Array<string>> {
    return this.getModelToSeparatedStringArray('phoneNumberTypes');
  }

  private getModelToSeparatedStringArray(
    modelName: 'emailAddressTypes' | 'postalAddressTypes' | 'phoneNumberTypes',
  ): Observable<Array<string>> {
    return this.model
      ? this.model.property<string>(modelName, '').pipe(map(this.convertCommaSeparatedStringToStringArray))
      : of(defaultProperties[modelName]);
  }

  private convertCommaSeparatedStringToStringArray(value: string) {
    return value ? value.split(',').map((arrayItem) => arrayItem.trim()) : [];
  }

  private getMaxEmailAddressesPreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('maxEmailAddresses', defaultProperties.maxEmailAddresses)
      : of(defaultProperties.maxEmailAddresses);
  }

  private getMaxPhoneNumbersPreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('maxPhoneNumbers', defaultProperties.maxPhoneNumbers)
      : of(defaultProperties.maxPhoneNumbers);
  }

  private getMaxPostalAddressesPreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('maxPostalAddresses', defaultProperties.maxPostalAddresses)
      : of(defaultProperties.maxPostalAddresses);
  }

  getLegalEntityDebouncePreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('legalEntityDebounceTime', defaultProperties.legalEntityDebounceTime)
      : of(defaultProperties.legalEntityDebounceTime);
  }

  getUsernameDebouncePreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('usernameDebounceTime', defaultProperties.usernameDebounceTime)
      : of(defaultProperties.usernameDebounceTime);
  }

  getRequiredUserActionsDebouncePreference(): Observable<number> {
    return this.model
      ? this.model.property<number>(
          'requiredUserActionsDebounceTime',
          defaultProperties.requiredUserActionsDebounceTime,
        )
      : of(defaultProperties.requiredUserActionsDebounceTime);
  }

  getLegalEntityResponseSizePreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('legalEntityResponseSize', defaultProperties.legalEntityResponseSize)
      : of(defaultProperties.legalEntityResponseSize);
  }

  getShowWidgetHeaderPreference(): Observable<boolean> {
    return this.model
      ? this.model
          .property<string>('showWidgetHeader', JSON.stringify(defaultProperties.showWidgetHeader))
          .pipe(map((value) => !!value && value !== 'false'))
      : of(defaultProperties.showWidgetHeader);
  }
}
