import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { GetElectronicAddress } from '../../../common-types';

@Component({
  template: '',
})
export class ManageEmailAddressesComponent implements OnInit {
  /**
   * Whether the manage button is disabled.
   */
  @Input() disableManage: boolean | undefined;
  /**
   * The list of email addresses stored.
   */
  @Input() emailAddresses: Array<GetElectronicAddress> | undefined;
  /**
   * The list of email address types.
   */
  @Input() emailAddressTypes: Array<string> | undefined;
  /**
   * The maximum number of allowed email addresses.
   */
  @Input() maxEmailAddresses: number | undefined;
  @Input() dismissTimeout: number | undefined;
  @Output() updateProfile = new EventEmitter<void>();
  @Output() manageEmail = new EventEmitter<void>();
  @Output() cancelManageEmail = new EventEmitter<void>();
  isManagingEmailAddressIndex: number | undefined = undefined;
  isAddingEmailAddress = false;
  isAddButtonAvailable = true;

  ngOnInit(): void {
    if (this.emailAddresses && this.maxEmailAddresses) {
      this.isAddButtonAvailable = this.emailAddresses.length < this.maxEmailAddresses;
    }
  }

  handleManageEmailOpen(index?: number) {
    this.isManagingEmailAddressIndex = index;
    this.manageEmail.emit();
  }

  handleManageEmailClosed() {
    this.isManagingEmailAddressIndex = undefined;
    this.isAddingEmailAddress = false;
    this.cancelManageEmail.emit();
  }

  handleAddNewEmailAddress() {
    if (this.disableManage) {
      return;
    }
    this.isAddingEmailAddress = true;
    this.manageEmail.emit();
  }

  updateUserProfileData() {
    this.updateProfile.emit();
  }
}
