import { Component, Input, OnInit } from '@angular/core';
import { PostalAddress } from '../../../common-types';
import { NewAddressComponent } from '../new-address/new-address.component';

@Component({
  template: '',
})
export class NewPostalAddressComponent extends NewAddressComponent implements OnInit {
  /**
   * The list of postal address types.
   */
  @Input() postalAddressTypes: Array<string> | undefined;

  newPostalAddressData: PostalAddress | undefined;

  ngOnInit(): void {
    this.newPostalAddressData = {
      type: this.postalAddressTypes && this.postalAddressTypes.length ? this.postalAddressTypes[0] : '',
    };
  }
}
