import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class UtilityService {
  getBody<T>(data: HttpResponse<T>): T {
    if (!data.body) {
      throw new Error('No body found in resource');
    }

    return data.body;
  }
}
