import { combineLatest, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

export const filterInputParameters = <T>(input: Observable<T | undefined>, prop: Observable<T>): Observable<T> =>
  combineLatest([input, prop]).pipe(
    map(([inputVal, propVal]: [any, any]) => (inputVal !== undefined ? inputVal : propVal)),
  );

export const convertCommaSeparatedStringToStringArray = (value: string) =>
  value ? value.split(',').map((arrayItem) => arrayItem.trim()) : [];
