import { Injectable } from '@angular/core';
import {
  LegalEntitiesHttpService,
  LegalEntityItemBase,
  SearchSubEntitiesParameters,
} from '@backbase/data-ang/accesscontrol';
import { Observable } from 'rxjs';

export { SearchSubEntitiesParameters } from '@backbase/data-ang/accesscontrol';

@Injectable()
export class LegalEntityDataService {
  constructor(private readonly legalEntityData: LegalEntitiesHttpService) {}

  getLegalentitiesSubEntities(
    searchSubEntitiesParameters: SearchSubEntitiesParameters,
  ): Observable<LegalEntityItemBase[]> {
    return this.legalEntityData.postSubEntities({ searchSubEntitiesParameters });
  }
}
