import { HttpResponse } from '@angular/common/http';
import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { NotificationService } from '@backbase/ui-ang/notification';
import { BehaviorSubject, combineLatest, Observable, of, Subscription } from 'rxjs';
import { catchError, map, publish, tap } from 'rxjs/operators';
import { ManageProfilePreferencesService } from '../../../services/manage-profile/manage-profile-preferences.service';

@Component({
  template: '',
})
export class EditAddressComponent implements OnDestroy {
  @Output() close = new EventEmitter<void>();
  @Output() closeAfterSave = new EventEmitter<void>();
  @ViewChild('notificationUpdateSuccess') notificationUpdateSuccessTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationDeleteSuccess') notificationDeleteSuccessTemplate: TemplateRef<any> | undefined;
  @ViewChild('notificationFailure') notificationFailureTemplate: TemplateRef<any> | undefined;

  @Input() set notificationDismissTime(value: number | undefined) {
    this.notificationDismissTimeSubject.next(value);
  }

  isSubmitting = false;
  private dismissTimeout: number | undefined;
  private readonly notificationDismissTimeSubject = new BehaviorSubject<number | undefined>(undefined);

  dismissTimeoutPreferenceSubscription: Subscription = combineLatest([
    this.notificationDismissTimeSubject.asObservable(),
    this.manageProfilePreferencesService.getNotificationDismissTimePreference(),
  ])
    .pipe(map(([input, preference]: [any, any]) => (input !== undefined ? input : preference * 1000)))
    .subscribe({
      next: (val) => (this.dismissTimeout = val),
    });

  constructor(
    protected readonly manageProfilePreferencesService: ManageProfilePreferencesService,
    protected readonly cd: ChangeDetectorRef,
    protected readonly notificationService: NotificationService,
  ) {}

  handleCancelClick() {
    this.close.emit();
  }

  readonly handleUpdateResponse =
    () =>
    (source: Observable<HttpResponse<undefined>>): Observable<HttpResponse<undefined> | undefined> =>
      source.pipe(tap(this.onFormSubmissionSuccessful), catchError(this.onFormSubmissionUnsuccessful), publish());

  readonly handleDeleteResponse =
    () =>
    (source: Observable<HttpResponse<undefined>>): Observable<HttpResponse<undefined> | undefined> =>
      source.pipe(tap(this.onDeletionSuccessful), catchError(this.onFormSubmissionUnsuccessful), publish());

  private readonly onFormSubmissionSuccessful = () => {
    this.isSubmitting = false;
    this.onSuccess(this.notificationUpdateSuccessTemplate as TemplateRef<any>);
  };

  private readonly onDeletionSuccessful = () => {
    this.isSubmitting = false;
    this.onSuccess(this.notificationDeleteSuccessTemplate as TemplateRef<any>);
  };

  private onSuccess(template: TemplateRef<any>) {
    this.notificationService.showNotification({
      message: template,
      modifier: 'success',
      ttl: this.dismissTimeout,
    });
    this.cd.markForCheck();
    this.closeAfterSave.emit();
  }

  private readonly onFormSubmissionUnsuccessful = (): Observable<undefined> => {
    this.isSubmitting = false;
    this.notificationService.showNotification({
      message: this.notificationFailureTemplate as TemplateRef<any>,
      modifier: 'error',
      ttl: this.dismissTimeout,
    });
    this.cd.markForCheck();

    return of(undefined);
  };

  ngOnDestroy() {
    this.dismissTimeoutPreferenceSubscription.unsubscribe();
  }
}
